.contactListItem {
  width: 30%;
  margin: 0 auto;
  padding: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 500;
  font-style: italic;
}
.contactListItem p {
  margin-right: 20px;
}
.deleteButton {
  display: inline-block;
  padding: 0px 10px;
  margin-right: 30px;
  border-radius: 10px;
  border: none;
  transition: all 0.25s ease-in-out;
}
.deleteButton:hover {
  box-shadow: 0px 0px 3px 3px rgba(137, 145, 135, 0.94);
  font-weight: 700;
  cursor: pointer;
}
